import React from 'react';
import { render } from '@testing-library/react-native';
import ButtonSpinner from '../../../../src/components/ui/Button/ButtonSpinner';

// This is the most reliable way to mock a component from 'react-native'.
// We create a mock and make sure it's loaded before anything else.
// The actual mock implementation will be placed in a __mocks__ folder.
jest.mock('react-native', () => {
  const RN = jest.requireActual('react-native');
  // We explicitly mock ActivityIndicator and ensure it's a spyable function
  RN.ActivityIndicator = jest.fn(() => null);
  return RN;
});

describe('ButtonSpinner', () => {
  // It's a good practice to clear mocks before each test
  beforeEach(() => {
    // We get the mocked component here to assert on its calls
    const { ActivityIndicator } = require('react-native');
    ActivityIndicator.mockClear();
  });

  it('devrait rendre un ActivityIndicator avec les props size et color', () => {
    const mockSize = 'large';
    const mockColor = '#FF0000';
    render(<ButtonSpinner size={mockSize} color={mockColor} />);
    
    // We get the mocked component from the required module
    const { ActivityIndicator } = require('react-native');
    expect(ActivityIndicator).toHaveBeenCalledTimes(1);
    expect(ActivityIndicator).toHaveBeenCalledWith(
      expect.objectContaining({
        size: mockSize,
        color: mockColor,
      }),
      {}
    );
  });
});